body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  background-color: #ededed; }

body > div {
  height: 100%; }

*:focus {
  outline: none; }

a {
  color: white;
  text-decoration: none; }

/* disable blue glow on the imported react-select component */
:global .is-focused:not(.is-open) > .Select-control {
  border-color: #d9d9d9 #ccc #b3b3b3;
  -webkit-box-shadow: none;
          box-shadow: none; }

.container {
  height: 100%;
  background-color: #ededed; }



