.header {
  background-color: #00AA8D;
  padding: 20px;
  color: white;
  overflow: hidden; }
  .header h1 {
    font-size: 16px;
    font-weight: normal;
    margin: 0px; }
  .header h2 {
    font-size: 12px;
    font-weight: 100;
    margin: 5px 0 0 0; }

.closeBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 4px;
  font-size: 12px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer; }
  .closeBtn:hover {
    color: rgba(0, 0, 0, 0.5);
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2); }



